@mixin rwd($screenmax, $screenmin) {
    @media (max-width: ($screenmax+'px')){
        @media (min-width: ($screenmin + 'px')) {
            @content;
        }
    }
}

@include rwd(820, 0) {
    footer {
        height: 20vh !important;
    }
    .footer1 {
        row-gap: 80px !important;
        padding-left: 5px;
    }
    .conclusion {
        height: 210vh;
    }
    .conclusion__img {
        width: 350px;
    }
    #captain {
        padding-right: 20px !important;
    }
    .team {
        grid-template-rows: repeat(5, auto);
        grid-template-columns: auto;
        row-gap: 10px;
    }

    #block {
        &1, &2, &3, &4, &5 {
            grid-column: 1/2;
        }
    }
    video {
        width: 350px !important;
        height: 197px !important;
        align-self: center !important;
    }
    .titleblock {
        grid-template-columns: 40% 350px;
        height: 120vh;
    }

    .achievements {
        height: 180vh;
        grid-template-rows: 47.45% 30%;
    }

    .achievements-carousel-container {
        grid-template-columns: auto 92% auto;
    }
}

@include rwd(1200, 821) {
    video {
        width: 500px !important;
        height: 281px !important;
        align-self: center !important;
    }
    .titleblock {
        grid-template-columns: 45% 500px !important;
    }

    .footer1 {
        padding-left: 5px;
        row-gap: 40px !important;
    }
    
    footer {
        grid-template-columns: 2fr 3fr !important;
        height: 16vh !important;
    }

    .conclusion {
        height: 130vh;
    }

    .achievements {
        height: 130vh;
    }

    .memes-picsGallery__img {
        width: 184px;
    }

    .team {
        grid-template-rows: repeat(5, auto);
        grid-template-columns: auto;
        row-gap: 10px;
    }

    #block {
        &1, &2, &3, &4, &5 {
            grid-column: 1/2;
        }
    }
    header {
        grid-template-columns: 12% 70% 12% !important;
    }
    .achievements-carousel-container {
        grid-template-columns: auto 70% auto;
    }
}

@include  rwd(1349, 1201) {
    .onerow {
        height: 300px;
    }
    .team-block-header-info {
        grid-template-rows: repeat(2, 20%) 60%;
    }
    .team-block-header-info-list{
        grid-template-rows: repeat(2, 35%);
    }
    .team-block {
        row-gap: 15px;
        padding-bottom: 10px;
    }
    footer {
        grid-template-columns: 4fr 4fr !important;
        height: 18vh !important;
    }
    #captain {
        padding-right: 39px !important;
    }
    .conclusion {
        height: 168vh !important;
    }
    .memes {
        height: 118vh !important;
        margin-bottom: 100px;
    }
    .achievements {
        height: 119vh !important;
        margin-top: 35px !important;
        grid-template-rows: 67.45% 60% !important;
        margin-bottom: 224px !important;
    }
    .footer1 {
        padding-left: 10px;
    }
    .team {
        height: 155vh;

        &-block-header {
            grid-template-columns: 145px auto 12.3%;

            &__img {
                height: 171px;
                width: 151px;
            }
        }
    }
    #mainpic {
        max-width: 800px;
        max-height: 540px;
    }
    #generated-mainpic {
        max-width: 800px;
        max-height: 540px;
    }
    .header-nav {
        grid-template-columns: repeat(3, 33%);
    }
}