.conclusion {
    //border-top: 1px solid #aaa;
    display: grid;
    height: 93vh;
    align-self: center;
    grid-template-columns: 45% 40%;
    justify-content: space-around;
    grid-template-rows: repeat(4, auto);
    align-content: center;

    &__img {
        border-radius: 7px;
        align-self: center;
        justify-self: center;
        grid-column: 1/2;
        grid-row: 3/5;
        max-width: 450px;
    }

    & > h1 {
        grid-column: 2/3;
        grid-row: 1/2;
    }
}

#introduction {
    display: grid; 
    grid-column: 1/2; 
    grid-row: 2/3; 
    align-content: flex-end;
    padding-left: 10px;
    font-size: 17.5px;
    font-style: italic;
}

#why {
    grid-row: 2/5;
    background-color: #ddd;
    border-radius: 7px;
    padding-left: 10px;
    padding-right: 10px;
}

#qwe {
    width: 100%;
    height: 50%;
    align-self: flex-end;
    justify-content: center;
    border-top: 7px dotted #aaa;
}