.memes {
    display: grid;
    height: 93vh;
    padding-top: 20px;
    border-top: 1px solid #aaa;

    &-buttons {
        display: grid;
        grid-template-columns: repeat(2, 20%);
        grid-template-rows: 50px;
        justify-content: center;
        column-gap: 6%;
        align-content: center;

        &__button {
            height: 50px;
            border: 2px solid #888;
            font-weight: 600;
        }
    }

    &-picsGallery {
        display: grid;
        grid-template-columns: repeat(4, auto);
        justify-content: space-around;

        &__img {
            object-fit: contain;
            border-radius: 27px;
            width: 276px;
            height: 186px;
            cursor: pointer;
        }
    }
}

.appendingimg {
    z-index: 100000;
    display: none;
    position: fixed;
    top: 7vh;
    width: 100vw;
    height: 93vh;
    justify-content: center;
    align-content: center;

    &-bottom {
        min-height: 40px;
        background-color: #eaeaea;
        font-weight: 800;
        display: grid;
        grid-template-columns: 20% 60% 20%;
        border: 1px solid #888;
        border-bottom-left-radius: 7px;
        border-bottom-right-radius: 7px;
        
        &__text {
            text-align: center;
            align-self: center;
            grid-column: 2/3;
            font-weight: 600;
        }

        &__text1 {
            text-align: center;
            align-self: center;
            grid-column: 2/3;
        }

        &__close {
            border: 1px solid #888;
            align-self: center;
            height: 60%;
            width: 80%;
            justify-self: center;
            grid-column: 3/4;
        }
    }

    & > img {
        justify-self: center;
        max-width: 1080px;
        max-height: 720px;
    }
}

.memes-picsGenerator {
    justify-self: center;
    display: grid;
    grid-template-columns: auto;
    max-width: 1080px;
    justify-content: center;
}

#generated-mainpic {
    justify-self: center;
    max-width: 1080px;
    max-height: 720px;
}