.titleblock {
    display: grid;
    background-color:#EDF6ED;
    background-size: 100%;
    grid-template-columns: 40% 622px;
    grid-template-rows: auto 15%;
    justify-content: space-around;
    margin-top: 7vh;
    height: 100vh;
    align-content: center;
    row-gap: 40px;
    //border-bottom: 1px solid #aaa;

    &-text {
        display: grid;
        grid-template-rows: repeat(3, auto);
        text-align: center;

        &__title {
            display: grid;
            align-content: center;
            font-size: 70px;
            margin: 0;
        }

        &__description {
            display: grid;
            align-content: center;
        }

        &__imp {
            font-weight: 600;
        }
    }

    &-video {
        display: grid;
    }

    &-video__video {
        border-radius: 10px;
        height: 350px;
        width: 622px;
    }

    &__bottomtext {
        display: grid;
        align-content: flex-end;
        text-align: center;
        grid-column: 1/3;
        color: #aaa;
        //text-shadow: -1px -1px 0 #bbb, 1px -1px 0 #bbb, -1px 1px 0 #bbb, 1px 1px 0 #bbb;
    }
}