.header {

    &-container {
        background-color: #F5EAFA;
        width: 100%;
        display: grid;
        grid-template-columns: 90%;
        justify-content: center;
        border-bottom: 1px solid rgb(146, 146, 146);
        position: fixed;
        height: 7vh;
        align-content: center;
        z-index: 100000;
    }

    display: grid;
    grid-template-columns: 12% 50% 12%;
    justify-content: space-between;

    &-nav {
        display: grid;
        grid-template-columns: repeat(3, 25%);
        justify-content: space-between;
    }

    &__name {
        display: grid;
        align-content: center;
        margin: 0;
        text-align: center;
    }

    &__button {
        width: 47px;
    }
}

.button {
    border-radius: 7px;
}