.team {
    height: 89vh;
    display: grid;
    row-gap: 90px;
    grid-template-columns: 30% 10% 20% 10% 30%;
    grid-template-rows: repeat(3, auto);
    padding-top: 2vh;
    padding-bottom: 2vh;
    border-bottom: 1px solid #aaa;
    align-content: center;

    &-block {
        background-color: #fffaf4;
        border-radius: 10px;
        border: 2px #d2d2d2 solid;
        display: grid;
        grid-template-rows: repeat(2, auto);
        width: 100%;
        height: auto;
        justify-self: center;

        &-header {
            column-gap: 5%;
            display: grid;
            grid-template-columns: 170px auto 8%;

            &__img {
                object-fit: cover;
                padding-top: 5px;
                padding-left: 5px;
                border-radius: 10px;
                height: 200px;
                width: 170px;
            }

            &-info {
                display: grid;
                align-content: center;
                grid-template-rows: repeat(2, 20%) 40%;
                
                &__fio {
                    font-weight: 600;
                    display: grid;
                    align-content: center;
                    text-align: center;
                }

                &__text {
                    font-weight: 800;
                    display: grid;
                    text-align: center;
                    align-content: center;
                }

                &-list {
                    display: grid;
                    grid-template-rows: repeat(2, 15%);
                    row-gap: 55px;
                }
            }

            &__arrow {
                align-self: center;
                color: #d2d2d2;
                font-size: 60px !important;
            }
        }

        &-body {
            padding-left: 10px;
            padding-top: 10px;
            padding-bottom: 10px;
            max-height: 900px;
            transition: 0.3s;
        }
    }
}

.hidden {
    opacity: 0;
    max-height: 0;
    padding-bottom: 0;
}

.onerow {
    grid-template-rows: auto !important;
    height: 210px;
}

.notDisplayed {
    display: none !important;
}

#block1 {
    grid-column: 2/5;
}
#block2 {
    grid-column: 1/3;
}
#block3 {
    grid-column: 4/6;
}
#block4 {
    grid-column: 1/3;
}
#block5 {
    grid-column: 4/6;
}