.achievements {
    display: grid;
    margin-top: 68px;
    height: 93vh;
    align-content: space-between;
    grid-template-rows: 47.45% 60%;
    row-gap: 66px;
    
    &-table {
        border-radius: 15px;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: auto 10%;
        z-index: 1000;
        font-style: italic;

        & > button {
            font-size: 18px;
        }

        &-col {
            display: grid;
            grid-template-rows: 45px auto;
            border-right: 1px solid black;
            background-color: white;
    
    
            &__header {
                font-weight: 600;
                padding-top: 5px;
                text-align: center;
                border-bottom: 1px solid #000;
            }

            &-info {
                display: grid;
                row-gap: 10px;
                background-color: white;
                padding-right: 0;

                &-double {
                    grid-template-columns: repeat(2, 1fr);

                    &__title {
                        font-weight: 600;                        
                        margin-bottom: 10px;
                        height: 19px;
                        text-align: center;
                        border-bottom: 1px solid #000;
                        padding-right: 0;
                    }
                }

                &-col {
                    display: grid;
                    grid-template-rows: 20px auto;
                    row-gap: 10px;
                    background-color: white;
                }

                &__item {
                    padding-left: 10px;
                    padding-right: 5px;
                }

                &-first {
                    border-right: 1px solid black;
                }
            }
        }
    }

    &-carousel {
        width: 60%;
        display: grid !important;
        height: 410px;

        &-container {
            height: 410px;
            display: grid;
            grid-template-columns: auto 60% auto;
            justify-content: center;
        }

        &-item {
            border-radius: 7px;
            text-align: center;
            display: grid;
            justify-self: center;
            height: 400px;
            width: 622px;
            border: 1px solid black;
            background-color: #ddd;
            font-weight: 600;

            &__img {
                background-color: #ddd;
                object-fit: cover;
                height: 350px;
                width: auto;
                border-radius: 7px;
            }
        }
    }
}

.owl-item {
    display: grid;
}

#trener {
    row-gap: 20px;
}

#firstcol {
    grid-template-rows: 25px 20px auto !important;
    border-left: none;
    border-top-left-radius: 7px;
}

#liniya {
    border-bottom: none;
}

#lastcol {
    border-right: none !important;
    border-top-right-radius: 7px;
}

.tohide {
    display: grid;
    row-gap: 10px;
}

.none {
    display: none;
}

#asd {
    grid-column: span 2;
}

#leftarrow {
    cursor: pointer;
    align-self: center;
    font-size: 70px;
    color: #ababab;
}

#rightarrow {
    cursor: pointer;
    align-self: center;
    font-size: 70px;
    color: #ababab;
}