@use 'header/header';
@use  'main/main';
@use 'footer/footer';

@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,400;0,500;0,600;0,800;1,400&display=swap');

body {
    display: grid;
    margin: 0;
    font-family: 'Nunito';
}


button {
    transition: all 0.3s;
    box-shadow: none;
    font-family: 'Nunito';
    font-weight: 600;
    border-radius: 7px;
    border: 1px solid black;
    cursor: pointer;
}

button:hover{
    box-shadow: 2px 12px 26px 5px rgba(34, 60, 80, 0.2);
}

.a__button:hover {
    box-shadow: 2px 12px 26px 5px rgba(34, 60, 80, 0.2) !important;
}

.a__button {
    transition: all 0.3s;
    font-family: 'Nunito';
    font-weight: 600;
    border: 2px rgb(146, 146, 146) solid;
    color: #424242;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 20px;
    margin: 4px 2px;
    cursor: pointer;
}